<template>
  <div class="main-div">
    <!-- {{ this.selectedReport}} -->
    <br>
    <b-row>
      <div class="center">
      <h5 class="center">Reports list</h5>
      </div>
    </b-row>
    <br>
    <div class="table-div" >
      <b-container fluid>
        <b-table
          striped
          class="table table-bordered table-sm"
          thead-class="thead-light"
          selectable
          :select-mode="selectMode"
          selected-variant="warning"
          @row-selected="onRowSelected"
          :items="reportItems"
          :fields="fields"
          :current-page="currentPage"
          :per-page="perPage"
          :filter="filter"
          :sort-by.sync="sortBy"
          :sort-desc.sync="sortDesc"
          :sort-direction="sortDirection"
          :hover="true"
        >
          <template v-slot:cell(selected)="{ rowSelected }">
            <template v-if="rowSelected">
              <span class="sr-only">Selected</span>
            </template>
            <template v-else>
              <span class="sr-only">Not selected</span>
            </template>
          </template>
          <!-- <template v-slot:cell(action)="row">
            <b-button v-show="showReportBtn" size="sm" @click="runReport(row.item)">{{
              "Show"
            }}</b-button>
          </template> -->
        </b-table>
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          size="sm"
          class="my-0"
        ></b-pagination>
      </b-container>
    </div>
    <b-form-row v-show="dateParameter">
      <b-col cols="2">
        <b-form-group
          label="FromDate"
          label-for="rptFromDate"
          label-align="left"
        >
          <b-input-group>
            <datepicker
              placeholder="Select Date"
              v-model="rptFromDate"
              format="dd/MM/yyyy"
              input-class="form-control form-control-sm styleLine"
            ></datepicker>
          </b-input-group>
        </b-form-group>
      </b-col>
      <b-col cols="2">
        <b-form-group label="To Date" label-for="rptToDate" label-align="left">
          <b-input-group>
            <datepicker
              placeholder="Select Date"
              v-model="rptToDate"
              format="dd/MM/yyyy"
              input-class="form-control form-control-sm styleLine"
            ></datepicker>
          </b-input-group>
        </b-form-group>
      </b-col>
    </b-form-row>
    <b-form-row v-show="departmentParameter">
      <b-col cols="2">
        <b-form-group
          label="Division"
          label-for="divCode"
          label-align="left"
          class="mb-1 textBoxWidth"
        >
          <b-input-group>
            <b-form-input
              class="form-control form-control-sm styleLine"
              style="padding-left: 1px"
              id="divCode"
              v-model="divCode"
            ></b-form-input>
          </b-input-group>
        </b-form-group>
      </b-col>
      <b-col cols="2">
        <b-form-group
          label="Department"
          label-for="rptDepartment"
          label-align="left"
          class="mb-1 textBoxWidth"
        >
          <b-input-group>
            <b-form-input
              class="form-control form-control-sm styleLine"
              style="padding-left: 1px"
              id="rptDepartment"
              v-model="deptCode"
            ></b-form-input>
          </b-input-group>
        </b-form-group>
      </b-col>
    </b-form-row>
       <b-form-row v-show="employeeParameter">
      <b-col cols="2" >
        <b-form-group
          label="Employee"
          label-for="empCode"
          label-align="left"
          class="mb-1 textBoxWidth"
        >
          <b-input-group>
            <b-form-input
              class="form-control form-control-sm styleLine"
              style="padding-left: 5px"
              id="empCode"
              v-model="empCode"
            ></b-form-input>
          </b-input-group>
        </b-form-group>
      </b-col>
    </b-form-row>
    <hr />
    <b-form-row>
      <b-col cols="2">
        <b-button variant="secondary" @click="runReport()">
          {{ "Show Report" }}</b-button
        >
      </b-col>
    </b-form-row>
  </div>
</template>

<script>
import axios from 'axios'
import { mapGetters } from 'vuex'
import Datepicker from 'vuejs-datepicker'
// import searchSm from '../components/SearchSm.vue'
const items = []
export default {
  data () {
    return {
      directorateList: [],
      errors: [],
      selectMode: 'single',
      selected: [],
      selectedReport: '',
      rptFromDate: '',
      rptToDate: '',
      reportItems: [],
      dateParameter: false,
      deptCode: '',
      divCode: '',
      empCode: 0,
      departmentParameter: false,
      employeeParameter: false,
      searchDepartment: {
        attr1: { name: 'reportId', label: 'Report Id' },
        attr2: { name: 'reportName', label: 'Report Name' }
      },
      fields: [
        {
          key: 'reportId',
          label: 'Report Id',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left'
        },
        // {
        //   key: 'reportName',
        //   label: 'Report name',
        //   sortable: true,
        //   sortDirection: 'desc',
        //   class: 'text-left'
        // },
        {
          key: 'reportDescription',
          label: 'Report Description',
          sortable: true,
          sortDirection: 'desc',
          class: 'text-left',
          tdClass: 'col-width-300'
        }
        // ,
        // { key: 'action', label: 'Show Report' }
      ],
      currentPage: 1,
      perPage: 10,
      totalRows: items.length,
      pageOptions: [5, 10, 15],
      sortBy: null,
      sortDesc: false,
      sortDirection: 'asc',
      filter: null
    }
  },
  computed: {
    ...mapGetters([])
  },
  components: {
    Datepicker
    // searchSm
    // baseList
  },
  mounted () {
    this.getReportList()
  },
  methods: {
    async getReportList () {
      const url = process.env.VUE_APP_API_URL + '/reports'
      await axios
        .get(url, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then((response) => {
          // console.log('=== No Error returned ====')
          this.reportItems = response.data
          this.totalRows = this.reportItems.length
        })
        .catch((error) => {
          console.log('Error ' + error.response)
        })
    },
    onFiltered (filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    },
    getCurrentPage: function (x) {},
    reSetParameterDisplay () {
      this.dateParameter = false
      this.departmentParameter = false
      this.employeeParameter = false
    },
    onRowSelected (x) {
      this.selectedReport = x[0].reportName
      if (typeof x[0] !== 'undefined') {
        switch (this.selectedReport) {
          case 'prs':
            this.reSetParameterDisplay()
            break
          case 'prs_p':
            this.reSetParameterDisplay()
            this.departmentParameter = true
            break
          case 'llo':
            this.reSetParameterDisplay()
            break
          case 'pslip-i':
            this.reSetParameterDisplay()
            this.employeeParameter = true
            break
          case 'pslip-all':
            this.reSetParameterDisplay()
            break
          default:
            break
        }
      }
    },
    checkParameter (x) {
      this.errors = []
      switch (x) {
        case 'XXCAS003':
          if (!this.rptTenderNo) {
            this.errors.push('Tender No')
          }
          break
        case 'XXCAS004':
          break
        default:
          break
      }
      if (!this.errors.length) {
        return true
      } else {
        this.errors.unshift(
          'Report parameters are required, please enter following '
        )
      }
    },
    formatDate (d) {
      var n = d.getMonth() + 1
      return d.getFullYear() + '-' + n + '-' + d.getDate()
    },
    runReport: function () {
      const x = this.selectedReport
      console.log('selected report:' + x)
      let fmDate = ''
      let toDate = ''
      // window.open(
      //   process.env.VUE_APP_REPORT_URL + 'prs&rs:Command=Render&rs:format=PDF'
      // )
      // this.checkParameter(x)
      // window.open(
      //         process.env.VUE_APP_REPORT_URL + `XXCASRCPTREG&rs:Command=Render&rs:format=PDF&FRDT=${fmDate}&TODT=${toDate}`
      //       )
      if (!this.errors.length) {
        switch (x) {
          case 'prs':
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'prs_p':
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&divCode=${this.divCode}&deptCode=${this.deptCode}`
            )
            break
          case 'llo':
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'llo-new':
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'pslip-i':
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&empCode=${this.empCode}`
            )
            break
          case 'pslip-all':
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF`
            )
            break
          case 'XXCAS004':
            // P_DIR_CODE todate, from date
            fmDate = this.formatDate(this.rptFromDate)
            toDate = this.formatDate(this.rptToDate)
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&P_DIR_CODE=${this.rptDirectorateCode}&P_DIR_CODE=${this.rptDirectorateCode}&p_from_dt=${fmDate}&p_to_dt=${toDate}`
            )
            break
          case 'XXCAS005':
            fmDate = this.formatDate(this.rptFromDate)
            toDate = this.formatDate(this.rptToDate)
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&P_TYPE=${this.rptType}&p_from_dt=${fmDate}&p_to_dt=${toDate}`
            )
            break
          case 'XXCAS006':
            //  todate, from date
            fmDate = this.formatDate(this.rptFromDate)
            toDate = this.formatDate(this.rptToDate)
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&p_from_dt=${fmDate}&p_to_dt=${toDate}`
            )
            break
          case 'XXCAS008':
            // year
            fmDate = this.formatDate(this.rptFromDate)
            toDate = this.formatDate(this.rptToDate)
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&p_from_dt=${fmDate}&p_to_dt=${toDate}`
            )
            break
          case 'XXCAS011':
            // from date, to date
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&p_ten_no=${this.tenderNo}`
            )
            break
          case 'XXCAS012':
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&p_ten_no=${this.tenderNo}`
            )
            break
          case 'XXCAS013':
            // from date, to date
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&p_ten_no=${this.tenderNo}`
            )
            break
          case 'XXCAS041':
            // from date, to date
            window.open(
              process.env.VUE_APP_REPORT_URL + `${x}&rs:Command=Render&rs:format=PDF&p_ten_no=${this.tenderNo}`
            )
            break
        }
      } else {
        alert(this.errors)
      }
    }
  }
}
</script>

<style scoped>
.main-div {
   width: 95%;
 margin: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: medium;
}
.table-div {
  width: 70%;
  margin: auto;
    font-size: medium;
}
.col-left {
  text-align: left;
}
.col-right {
  text-align: right;
}
.input-required {
  background-color: rgb(245, 242, 212);
}

#b-col {
  text-align: left;
}

.textBoxWidth {
  width: 160px;
}
.cellColor {
  background-color: #d9dcdf;
}
.calculated-value {
  background-color: #d9dcdf;
}
.sub-head {
  background-color: #d9dcdf;
}
.group1-color {
  background-color: rgb(242, 242, 242);
}
.group2-color {
  background-color: rgb(230, 230, 230);
}
p {
  margin-bottom: 0px;
}
.spacer {
  margin-top: 5px;
  margin-bottom: 5px;
}
.col-width-300 {
   width:  300px;
}
.center {
  margin-left: auto;
  margin-right: auto;
}

</style>
